<template>
    <ScrollView style="width:100%;height:50px;line-height:50px;border-bottom:1px solid #e7e7e7;">
    <cell v-for="(item,index) in list_cell" :cell_title="item" :key="index"
        :label_search="label_search"
        :label_list_index="label_list_index"/>
    </ScrollView>
</template>
<script>
import cell from './cell.vue';
export default {
    props:['list_cell','label_search','label_list_index'],
    components:{
        cell : cell
    }
}
</script>




