<template>
    <View style="width:100%;height:50px;line-height:50px;border-bottom:1px solid #e7e7e7;">
        <cell v-for="(item,index) in nav_list" :key="index" :cell_title="item" @nav_search="nav_search" :index="index" />
    </View>
</template>
<script>
import cell from './cell.vue';
export default {
    props:['nav_search'],
    data:function(){
        return {
            nav_list:['电视剧','电影','综艺','动漫']
        }
    },
    components:{
        cell : cell
    }
}
</script>



