<template>
    <View style="display:inline-block;width:33.3%;padding-top:45%;position:relative;">
        <Image :src="cellData.albumDocInfo.albumImg" style="position:absolute;left:0;top:0;right:0;bottom:0;max-width:100%;max-height:100%;" />
        <Text style="display: block; padding: 0 10px; height: 30px; line-height: 30px;overflow:hidden;">{{cellData.albumDocInfo.albumTitle}}</Text>
        <Text style="position:absolute;right:0;top:0;background:red;color:white;font-size:12px;">{{cellData.albumDocInfo.tag}}</Text>
    </View>
</template>
<script>
import cell from './cell.vue';
export default {
    props:['cellData'],
    components:{
        cell:cell
    }
}
</script>




