<template>
    <View>
        <search_input />
        <nav_list :nav_search="nav_search"/>
        <label_list :label_search="label_search"/>
        <result_list v-if="data" :data="data" />
    </View>
</template>
<script>
import search_input from './search_input.vue';
import nav_list from './nav/list.vue';
import label_list from './label/label.vue';
import result_list from './result/list.vue';

import { addParamsToUrl } from 'util';

const categoryUrl = 'https://search.video.iqiyi.com/o';
const categoryParams = {
    pageNum : 1,
    mode : 11,
    ctgName : '',
    threeCategory : '',
    pageSize : 12,
    type : 'list',
    "if" : 'html5',
    pos : 1,
    site : 'iqiyi',
    qyid : '',
    access_play_control_platform : 15,
    pu : '',
    u : '',
    from  : 'weixin_lite_app',
    ispurchase : ''
}
// three categoryArr make
const threeCategoryArr = [];
function threeCategoryMake (){
    let str = '';
    Array.isArray(threeCategoryArr) && threeCategoryArr.forEach((value , index)=>{
        str != '' && (str += ',');
        str += `${value};must`;
    })
    return str;
}
function categoryRequest(){
    qiyiApi.request(addParamsToUrl(categoryUrl,categoryParams),{},(result)=>{
        this.data = result;
    });
}


export default {
    components:{
        search_input : search_input,
        nav_list : nav_list,
        label_list : label_list,
        result_list : result_list
    },
    data:function(){
        return {
            data : false
        }
    },
    mounted:function(){
        categoryRequest.call(this);
    },
    methods:{
        nav_search:function( nav_name ){
            categoryParams.ctgName = nav_name;
            categoryRequest.call(this);
        },
        label_search:function(key , value ){
            threeCategoryArr[key] = value;
            categoryParams.threeCategory = threeCategoryMake();
            categoryRequest.call(this);
        },
        page_search:function(){

        }
    }
}
</script>



