<template>
    <View>
        <cell v-for="(item,index) in data.data.docinfos" :key="index" :cellData="item" />
    </View>
</template>
<script>
import cell from './cell.vue';
export default {
    props:['data'],
    components:{
        cell:cell
    }
}
</script>



