<template>
    <View>
        <list v-for="( item , index ) in label_list" :key="index" :list_cell="item" :label_search="label_search" :label_list_index="index" />
    </View>
</template>
<script>
import list from './list.vue';
export default {
    props:['label_search'],
    data:function(){
        return {
            label_list:[
                ['热搜榜','好评榜','新上线'],
                ['全部地区','国漫','日漫'],
                ['全部版本','TV版','OVA版','剧场版','真人版','特别版','电影版','短片'],
                ['全部风格','萝莉','轻改','漫改','原创','游戏','搞笑','经典','热血','萌系']
            ]
        }
    },
    components:{
        list : list
    }
}
</script>





